div.CarouselContainer {
  width: 100vw;
  height: 100%;
  padding: 0 8px;
  display: flex;
  align-items: center;
  justify-content: space-between;

  button.btnIndexComp {
    display: flex;
    align-items: center;
    justify-self: center;

    background: none;
    border: 1px solid black;
    border-radius: 6px;

    z-index: 5;

    transition: 0.5s;
    &:hover {
      transform: scale(1.05);
      box-shadow: 0 0 10px black;
      border-radius: 12px;
    }

    svg {
      width: 50px;
      height: 50px;
    }
  }
}
